.App {
  font-family: 'Instrument Sans';
  font-size: small;
  text-align: center;
  min-width: 320px;
  padding: 25px;
}

.content {
  position: relative;
  max-width: 300px;
}

.limited {
}

.info {
}

.top {
  height: 80px;
}

.PasswordPrompt {
  text-align: center;
  min-width: 200px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.footer-report-button {
  background-color: transparent !important;
  box-shadow: none !important;
}

.back-button {
  border-radius: 23
}

@media only screen and (hover: none) and (pointer: coarse) {
  .content {
    display: none;
  }
  .footer-report-button {
    width: 100%;
  }
  .version-text {
    margin-top: 15px !important;
    display: inline-block;
  }
}
